.slider-controls {
	button {
		position: absolute;
		top: 50%;
		width: 44px;
		height: 44px;
		margin-top: -22px;
		border-radius: 50%;
		cursor: pointer;
		background: #000;
		svg {
			display: flex;
			align-items: center;
			justify-content: center;
			margin: auto;
			path {
				fill: #fff;
			}
		}
		&:hover {
			background: rgba(0, 0, 0, 0.55);
			svg {
				path {
					fill: var(--primary-color);
				}
			}
		}
	}
	button[disabled] {
		opacity: 0;
		visibility: hidden;
	}
	.slider-prev-button {
		left: 10px;
	}
	.slider-next-button {
		right: 10px;
	}
}
.tns-nav {
	position: absolute;
	left: 0;
	right: 0;
	text-align: center;
	button {
		width: 5px;
		height: 5px;
		border-radius: 50%;
		margin: 0 5px;
		transition: all .25s ease-in-out;
		background: rgba(0,0,0,0.2);
		&:hover {
			background: #000;
		}
	}
	.tns-nav-active {
		background: #000;
	}
	@media (min-width: 750px) {
		button {
			width: 12px;
			height: 12px;
		}
	}
}