.section-testimonials {
	@import './.common/nav-dots';
	.tns-nav {
		position: relative;
		bottom: 0;
		margin: 20px 0;
	}
	.section-content-inner {
		position: relative;
	}
	.block-inner {
		background: #fff;
		border-radius: var(--border-radius);
		padding: 20px;
	}
	.reviews {
		p {
			margin: 0 0 10px;
			font-size: 14px;
			color: var(--text-color);
			line-height: 1.48em;
		}
	}
	.author {
		font-size: 13px;
		line-height: 1.48em;
		color: var(--subtext-color);
		margin: 10px 0;
	}
	.rating {
		line-height: 1.2em;
	}
	.section-bottom-button {
		margin-top: 20px;
		.btn {
			min-width: 170px;
		}
	}
	@media (min-width: 750px) {
		.section-bottom-button {
			margin-top: 30px;
		}
		.block-inner {
			padding: 30px;
		}
		.reviews {
			p {
				margin: 0 0 15px;
				font-size: 17px;
			}
		}
		.author {
			font-size: 15px;
			margin: 15px 0;
		}
	}
}